@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

[class|="btn"], [class*=" btn-"], input[type="submit"], input[type="button"], input[type="reset"], button {
	display: inline-block;
	vertical-align: top;
	position: relative;
	text-align: center;
	text-decoration: none;
	cursor: pointer; }

[class|="btn"], [class*=" btn-"] {
	& input[type="submit"], & input[type="button"], & input[type="reset"] {
		background: url("../../img/empty.png") repeat;
		display: block;
		@include cover-abs();
		padding: 0;
		border: none;
		z-index: 99;
		opacity: 0; } }

/* btn-default */
[class*="btn-default"] {
	position: relative;
	padding: 9px 78px 7px;
	border: 2px solid transparent;
	border-radius: 10px;
	margin: 0;
	min-width: 210px;
	@include font-alt;
	color: $text-color;
	text-transform: uppercase;
	background: $base-color-alt;
	box-shadow: 0 2px 3px rgba(black, 0.22);

	&, & > span {
		@include font(16px, 20px);
		letter-spacing: normal; }

	// hover
	.no-touchevents &:hover {
		border-color: darken($base-color-alt, 8%); }

	// disabled
	&:disabled, &.disabled, &[data-disabled] {
		cursor: default;
		background-color: $btn-disabled-color !important;
		border-color: transparent !important;
		box-shadow: none !important;
		color: #bfbfbf !important; } }

/* btn-default end */

/* btn-default-alt */
.btn-default-alt {
	color: white;
	background-color: $base-color;

	.no-touchevents &:hover {
		border-color: darken($base-color, 8%); } }

/* btn-default-alt end */

/* btn-with-icon */
.btn-with-icon {
	$btni-space: 10px;
	//=================
	text-align: center;
	@include clear-spacing;
	white-space: nowrap;

	svg, span {
		@include display-inline-block-middle; }

	path {
		fill: currentColor; }

	span {
		text-align: left;
		white-space: normal;
		padding-right: $btni-space; }

	svg + span {
		padding-right: 0;
		padding-left: $btni-space; } }

/* btn-with-icon end */

/* btn-with-arrow */
.btn-with-arrow {
	$arrow-width: 18px;
	$arrow-space: 10px;

	& > i, & > span {
		@include display-inline-block-middle; }

	& > i {
		position: relative;
		margin-right: $arrow-space;
		@include font(0, 0);
		@include size($arrow-width, 18px);

		&::before, &::after {
			@include pseudo();
			@include position(50%);
			border: 0 solid currentColor;
			transform: none; }

		&::before {
			@include size($arrow-width, 2px);
			border-top-width: 2px;
			margin: -1px 0 0 round(-$arrow-width/2); }

		&::after {
			top: 0;
			@include size(12px);
			border-width: 2px 0 0 2px;
			transform: rotate(-45deg);
			margin: 3px 0 0 round(-$arrow-width/2); } }

	& > span {
		text-align: left;
		white-space: normal;
		max-width: calc(100% - #{$arrow-width} - #{$arrow-space});

		& ~ i {
			margin-right: 0;
			margin-left: $arrow-space;

			&::after {
				left: auto;
				right: 0;
				border-width: 0 2px 2px 0;
				margin: 3px 0 0 1px; } } } }

/* btn-with-arrow end */

/* button popup close */
.btn-popup-close {
	//<a href="#" class="btn-popup-close js-btn-menu-close"><i></i><span>Закрыть</span></a>
	$btn-bg: #f3f3f3;
	$btn-color: $text-color;
	$btn-size: 60px;
	$btn-space: 12px;
	$btn-lines-width: 20px;
	$btn-lines-height: 3px;
	$btn-lines-space: 5px;
	$btn-lines-color: $text-color;

	@mixin line-style() {
		@include size($btn-lines-width, $btn-lines-height);
		border-radius: 2px; }

	display: block;
	@include size($btn-size);
	background: $btn-bg;
	color: white;
	text-decoration: none;
	@include clear-spacing;
	white-space: nowrap;
	transition-property: background-color, opacity, visibility;
	transition-duration: $animation-speed;

	@include media('<mob-lg') {
		@include size(45px); }

	&::before {
		content: '';
		display: inline-block;
		height: $btn-size;
		min-height: 100%;
		vertical-align: middle; }

	> span {
		@include hide-text; }

	> i {
		display: inline-block;
		vertical-align: middle;
		position: relative;
		backface-visibility: hidden;
		background-color: transparent;
		@include line-style;

		&::before, &::after {
			@include pseudo();
			left: 0;
			backface-visibility: hidden;
			@include line-style;
			background: $btn-lines-color;
			transition-property: background-color;
			transition-duration: $animation-speed; }

		//first line menu
		&::before {
			top: 0;
			transform: rotate(-45deg); }

		//third line menu
		&::after {
			top: 0;
			transform: rotate(45deg); } }

	.no-touchevents &:hover {
		background-color: $mark-color; } }

/* button popup close end */
