@import "../utils/_mixins";

// =============================================================================
// Font Face
// =============================================================================
@include font-face("Museo Sans Cyrl 300", "../fonts/museosanscyrl/museosanscyrl-300", normal, normal, eot woff ttf svg);
@include font-face("Museo Sans Cyrl 300 Italic", "../fonts/museosanscyrl/museosanscyrl-300italic", normal, normal, eot woff ttf svg);
@include font-face("Museo Sans Cyrl 500", "../fonts/museosanscyrl/museosanscyrl-500", normal, normal, eot woff ttf svg);
@include font-face("Museo Sans Cyrl 700", "../fonts/museosanscyrl/museosanscyrl-700", normal, normal, eot woff ttf svg);
@include font-face("Museo Sans Cyrl 700 Italic", "../fonts/museosanscyrl/museosanscyrl-700italic", normal, normal, eot woff ttf svg);
@include font-face("Museo Sans Rounded 700", "../fonts/museosansrounded/museosansrounded-700", normal, normal, eot woff ttf svg);
